@import "normalize";

#app {
  min-height: 100vh;
  display: block;
  background: rgb(255, 155, 112);
  background: -webkit-linear-gradient(45deg, rgba(215, 174, 120, 1) 0%, rgba(192, 127, 97, 1) 28%, rgba(117, 60, 66, 1) 50%, rgba(86, 46, 72, 1) 61%, rgba(63, 30, 51, 1) 78%, rgba(37, 12, 31, 1) 90%, rgba(31, 13, 29, 1) 96%, rgba(24, 10, 25, 1) 100%);
  background: -moz-linear-gradient(45deg, rgba(215, 174, 120, 1) 0%, rgba(192, 127, 97, 1) 28%, rgba(117, 60, 66, 1) 50%, rgba(86, 46, 72, 1) 61%, rgba(63, 30, 51, 1) 78%, rgba(37, 12, 31, 1) 90%, rgba(31, 13, 29, 1) 96%, rgba(24, 10, 25, 1) 100%);
  background: -o-linear-gradient(45deg, rgba(215, 174, 120, 1) 0%, rgba(192, 127, 97, 1) 28%, rgba(117, 60, 66, 1) 50%, rgba(86, 46, 72, 1) 61%, rgba(63, 30, 51, 1) 78%, rgba(37, 12, 31, 1) 90%, rgba(31, 13, 29, 1) 96%, rgba(24, 10, 25, 1) 100%);
  background: linear-gradient(45deg, rgba(215, 174, 120, 1) 0%, rgba(192, 127, 97, 1) 28%, rgba(117, 60, 66, 1) 50%, rgba(86, 46, 72, 1) 61%, rgba(63, 30, 51, 1) 78%, rgba(37, 12, 31, 1) 90%, rgba(31, 13, 29, 1) 96%, rgba(24, 10, 25, 1) 100%);
}

#form_wrapper {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: absolute;
  right: 25%;
  left: 25%;
  top: 19%;
}

form#auth_form {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-flow: column;
     -moz-box-orient: vertical;
     -moz-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  background-color: rgba(227, 227, 227, 0.25);
  padding: 25px;
  -webkit-border-radius: 8px;
     -moz-border-radius: 8px;
          border-radius: 8px;
  -webkit-box-shadow: rgba(0, 0, 0, 0.27) 15px 15px 0;
     -moz-box-shadow: rgba(0, 0, 0, 0.27) 15px 15px 0;
          box-shadow: rgba(0, 0, 0, 0.27) 15px 15px 0;

  & label {
    margin: 4px 0 4px 0;

    & input {
      min-width: 250px;
      background: rgba(255,255,255,0.54118);
      outline: none;
      border: none;
      -webkit-border-radius: 9px;
         -moz-border-radius: 9px;
              border-radius: 9px;
      padding: 3px;
    }
  }

  & #send {
    -webkit-align-self: end;
        -ms-flex-item-align: end;
            align-self: end;
    margin-top: 10px;
    background-color: aquamarine;
    outline: none;
    border: none;
    padding: 3px 20px;
    -webkit-border-radius: 4px;
       -moz-border-radius: 4px;
            border-radius: 4px;
    cursor: pointer;
    -webkit-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;

    &:hover {
      background-color: #2eb07a;
      color: #fff;
    }
  }

  & #error-form-msg {
    color: #ff4141;
    margin-top: 8px;
    text-align: center;
    font-weight: bold;
    background-color: rgba(0,0,0,0.6902);
    -webkit-border-radius: 5px;
       -moz-border-radius: 5px;
            border-radius: 5px;
    font-size: 14px;
  }
}

@media (min-width: 620px){
  form#auth_form {
    margin: 17% 0 0 0;
  }
}